import React from 'react';
import './Ejournal.css';
import {Tabs} from 'react-bootstrap'
import Switch from 'react-bootstrap/esm/Switch';
import { Route } from 'react-router-dom';
import { Tab } from './tab/ Tab.js';

const styles = {
    fontFamily: 'sans-serif',
  };
  
 export class Ejournal extends React.Component {
    constructor(props) {
      super(props);
      this.state = {active: 'aTab'};
    }
    render() {
      const content = {
        aTab: 'Tab A',
        bTab: 'Tab B',
        cTab: 'Tab C',
      };
      return (
         
            <>
        <br></br>  
        <br></br>
        <div className="App">
      <header className="App-header">
        
        <p>
          E-JOURNAL
        </p>
        </header>
        </div>
        <div style={styles}>
        <h1>Tabs Example</h1>
        <Tabs
          active={this.state.active}
          onChange={active => this.setState({active})}
        >
          <div key="aTab">A Title</div>
          <div key="bTab">B Very Long Title</div>
          <div key="cTab">{this.state.active === 'bTab' ? 'Title' : 'Very Long Title'}</div>
        </Tabs>
        <h2>Content</h2>
        <p>{content[this.state.active]}</p>
      </div>
     
      <Switch>
              <Route path="/tab">
                <Tab />
              </Route>
        </Switch>
  </>   
  
        )
        
    }
}
export default Ejournal;
        